@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Quicksand', -apple-system, BlinkMacSystemFont, 'Quicksand', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SearchInput{display:flex;justify-content:center;align-items:center}.SearchInput .found-stocks{position:absolute;background:grey;padding:20px;min-width:300px;border-radius:5px;z-index:100;opacity:.9}.SearchInput .found-stocks div{margin:10px}.SearchInput .found-stocks div .found-stock-description{margin:0 20px}.SearchInput .found-stocks div button{float:right}.SearchInput .found-stocks div:hover{cursor:pointer;transform:scale(1.01);transition:.5s}

.Logo{padding:10px 20px}

.CategoriesList .add-category-btn{float:right}.CategoriesList ul{list-style-type:none}.CategoriesList .category-header{margin-bottom:20px;padding:20px}.CategoriesList .category-item{padding:20px}.CategoriesList .category-item:hover{cursor:pointer;opacity:.5}.CategoriesList .category-item>*{vertical-align:middle}

.StocksList{padding:10px 20px}

.StockCard{display:flex;justify-content:space-between;align-items:center;margin:10px;padding:10px;height:80px;transition:.5s;background-color:#242424;border-radius:10px}.StockCard>div{flex:1 1;display:inline-block}.StockCard:hover{cursor:pointer;transform:scale(1.01)}.StockCard .stock-price .StockPriceChange{margin-left:10px}

.App{box-sizing:border-box;min-height:100vh;background-color:#242424;color:#fff}.App .grid{display:grid;grid-template-columns:1fr 9fr;grid-template-rows:100px 1fr;height:100vh}.App .scrollable-container{overflow:auto}.App .background-secondary{background-color:#333}

.StockFundamentals{margin:20px}.StockFundamentals .price-section .current-price{font-size:36px;font-weight:700}.StockFundamentals .price-section .StockPriceChange{font-size:30px;font-weight:500;margin-left:10px}

